@import url("../common.css");

.noteboard__container {
  width: 100%;
  overflow: scroll;
  white-space: nowrap;
}

.noteboard__columns {
  position: relative;
  left: 50px;
}

.columns {
  display: inline-block;
  border-right: 1px solid var(--main-color);
}

.column {
  display: inline-block;
}

.column--note {
  position: absolute;
}

.block {
  width: 50px;
  height: 40px;
  cursor: pointer;
  border: 0.5px solid #ccf2ed;
  text-align: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-direction: column;
      flex-direction: column;
}

.block-text {
  vertical-align: middle;
  font-size: 14px;
}

.block--selected {
  background-color: var(--main-color);
}

/* .block--border-bottom {
  border-bottom: 1px solid var(--main-color);
}

.block--border-top {
  border-top: 1px solid var(--main-color);
} */

.block-note {
  color: black;
  font-size: 15px;
  opacity: 0.5;
}

.block--selected .block-note {
  color: white;
  opacity: 1;
}



